export const START_LOADING = 'START_LOADING';
export const FINISH_LOADING = 'FINISH_LOADING';
export const GET_TABLE = 'GET_TABLE';
export const SET_TABLE = 'SET_TABLE';

export const getTable = () => ({
  type: GET_TABLE,
});

export const setTable = (table) => ({
  type: SET_TABLE,
  payload: table,
});

export const start = () => ({
  type: START_LOADING,
})

export const finish = () => ({
  type: FINISH_LOADING,
})