import { takeEvery, call, put } from 'redux-saga/effects';
import { GET_TABLE, setTable, start, finish } from '../actions/actions';
import http from '../../utils/http';

export function* get(action) {
  try {
    yield put(start())
    const data = yield call(http, {
      method: 'GET',
      url: 'https://script.googleusercontent.com/macros/echo?user_content_key=wVDXi0PzyGwqCpVENKf9zBkP07K22PbjZWyu6jQXBo8lgwGip06-Cw_CB_mXByms_Jzk4xdEynqRiyDEc-W7zWIwPu_eW-Zqm5_BxDlH2jW0nuo2oDemN9CCS2h10ox_1xSncGQajx_ryfhECjZEnDzAfLfFmLk-dbM3bjbhTyuU2X-3x2hX3kTozbgGNyrbw0gWSstgvvML0sC7yiEFBA3wRIWXNpiW&lib=Md2v0UwbwsfSs5wro0vxzVrQTBPWGJDZ2',
    });
    yield put(setTable(data.data))
    yield put(finish())
  } catch (e) {
    yield put(finish())
  }
}

export default function* test() {
  yield takeEvery(GET_TABLE, get);
}
