import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getTable } from '../../common/actions/actions';
import './main-page.css';
import { getFormat } from '../../utils/format';

class MainPage extends Component {
  componentDidMount() {
    const { getTable, table } = this.props;
    getTable();
  }

  renderTable = (data) => {
    const rgex = /[0-9.]{2,}/;
    return (
      <table>
        <thead>
          <tr>
            {data.result[0].map((item,i) => {
              return <th
                key={i}
                className={item ? (i === 1 ? 'left': 'right') : i > 5 ? 'col_without-item' : `col_min`}
              >
                {item}
              </th>
            })}
          </tr>
        </thead>
        <tbody>
            {data.result.map((item, index) => {
              if (index !== 0) {
                return (
                  <tr key={Math.random()}>
                    {item.map((item,i) => {
                      return <td
                        key={i}
                        className={item ? ((i === 1 ? 'left': 'right')+(rgex.test(item) ? '': ' bold')) : i > 5 ? 'col_without-item' : 'col_min'}
                      >
                        {rgex.test(item) ? getFormat(item) : item}
                      </td>
                    })}
                  </tr>
                );
              }
            })}
        </tbody>
      </table>
    );
  }

  render() {
    const { table } = this.props;
    return (
      <div className='table-container'>
        {table && table.result && table.result.length !== 0 ? this.renderTable(table) : null}
      </div>
    );
  }
}

export default connect(store => ({
  table: store.test.table,
}), {
  getTable,
})(MainPage);