import {
  START_LOADING,
  FINISH_LOADING,
  SET_TABLE,
} from '../actions/actions.js';

const initialState = {
  table: [],
  isLoading: false,
};

export default function test(state = initialState, action) {
  switch (action.type) {
    case START_LOADING:
      return {
        ...state,
        isLoading: true,
      }
    case FINISH_LOADING:
      return {
        ...state,
        isLoading: false,
      }
    case SET_TABLE:
      return {
        ...state,
        table: action.payload,
      }
    default:
      return state;
  }
}
