import axios from 'axios';

export default function http({ method, url, data, params }) {
  let config = {
    method: method.toLowerCase(),
    url,
    params,
    headers: {
      'Content-Type': 'application/json',
    }
  };
  if (data) config['data'] = data;
  
  axios.interceptors.response.use(response => {
    return response;
  }, error => {
    return error;
  });

  return axios(config);
}

