export const parseFromAmericanFormat = (americanFormatNumber) => {
    return americanFormatNumber.toString().replace(/[a-zA-z]/g, '').split('.').join(',');
  };
  
  export const getFormat = (incoming) => {
    const incomingData = parseFromAmericanFormat(incoming);
    console.log(incomingData)
    const regex9 = /\d{9}$/;
    const regex6 = /\d{6}$/;
    const regex3 = /\d{3}$/;
    const regFindFractional = /[,]\d+/;
    const excess = incomingData.match(regFindFractional) ? incomingData.match(regFindFractional)[0] : '';
    let onlyFourFractional = '';
    if (excess.length > 4) {
      onlyFourFractional = excess.match(/[,]\d{0,4}/)[0];
    }
    const value = incomingData.replace(excess, '');
    let result = null;
    if (value.length < 4) {
      result = value;
    }
    if (value.length > 3) {
      const res = value.replace(regex3, `.${value.match(regex3)}`);
      result = res;
    }
    if (value.length > 6) {
      const res = value.replace(regex6, `.${value.match(regex6)}`);
      const intermediate = res.replace(regex3, `.${res.match(regex3)}`);
      result = intermediate;
    }
    if (value.length > 9) {
      const res = value.replace(regex9, `.${value.match(regex9)}`);
      const intermediate = res.replace(regex6, `.${res.match(regex6)}`);
      const finalIntermediate = intermediate.replace(regex3, `.${res.match(regex3)}`);
      result = finalIntermediate;
    }
    const addiotionalValue = onlyFourFractional ? onlyFourFractional : excess;
    return result ? result + addiotionalValue : incomingData;
  };
  